import SearchBox from '@/components/SearchBox.vue'
const KEYWORD_LIST = 'keyword_list'
export default {
  components: {
    SearchBox
  },
  data () {
    return {
      keywordList: [],
      query: ''
    }
  },
  onShow () {
    this.keywordList = wx.getStorageSync(KEYWORD_LIST) || []
  },
  onHide () {
    this.query = ''
  },
  methods: {
    // 清除
    clearAll () {
      wx.showModal({
        title: '提示', // 提示的标题,
        content: '你确定要删除历史记录吗？', // 提示的内容,
        showCancel: true, // 是否显示取消按钮,
        cancelText: '取消', // 取消按钮的文字，默认为取消，最多 4 个字符,
        cancelColor: '#000000', // 取消按钮的文字颜色,
        confirmText: '确定', // 确定按钮的文字，默认为取消，最多 4 个字符,
        confirmColor: '#3CC51F', // 确定按钮的文字颜色,
        success: res => {
          if (res.confirm) {
          //  清除历史记录
            wx.removeStorageSync(KEYWORD_LIST)
            this.keywordList = ''
          }
        }
      })
    },
    toSearchList (keyword, noShow) {
      if (!noShow) {
        this.query = keyword
      }
      // 如果搜索列表中有Keyword，先去掉，再在头部增加keyword
      let _keywordList = this.keywordList.filter(v => {
        return v !== keyword
      })
      _keywordList.unshift(keyword)
      wx.setStorageSync(KEYWORD_LIST, _keywordList)
      // 跳转到search_list
      wx.navigateTo({ url: '/pages/search_list/main?query=' + keyword })
    }
  }
}
